	module.exports = {"data":{"markdownRemark":{"html":"<p>HackBot is HackerOne’s free automated service that provides in-context guidance, suggesting key features and actions on the platform in order to save you time and effort.</p>\n<p>For example, HackBot identifies potential duplicates and related reports to help you associate and close reports more quickly. It also can identify out of scope domains which frequently receive invalid reports and suggest a pre-submission trigger which, in turn, warns hackers in the act of submitting a report for that domain.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-89af6.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 69.1771269177127%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"hackbot\"\n        title=\"\"\n        src=\"/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-e7ffa.png\"\n        srcset=\"/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-49813.png 125w,\n/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-dc8a5.png 250w,\n/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-e7ffa.png 500w,\n/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-dda24.png 750w,\n/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-c5f18.png 1000w,\n/static/hackbot-d0cfaae6976ae71f253d0ede31b8e575-89af6.png 1434w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>HackBot offers suggestions that are visible only to your security team as internal comments. It will not take any action on your behalf.</p>\n<p>We love adding new functionality to HackBot to make your life easier and more efficient, so <a href=\"https://support.hackerone.com/hc/en-us/requests/new\">let us know</a> if you have any suggestions on how it can be more useful.</p>","frontmatter":{"path":"/programs/hackbot.html","id":"programs/hackbot","title":"Hackbot","description":null}}},"pathContext":{}}

